import * as consts from './consts';

export const getPromotionPage = payload => ({
    type: consts.GET_PROMOTION_PAGE_REQUEST,
    payload
});

export const getHomePosts = payload => ({
    type: consts.GET_HOME_POSTS_REQUEST,
    payload
});

export const getMorePosts = payload => ({
    type: consts.GET_MORE_POSTS_REQUEST,
    payload
});

export const getMorePostsError = error => ({
    type: consts.GET_MORE_POSTS_ERROR,
    error
});

export const resetHomePosts = () => ({
    type: consts.RESET_HOME_POSTS
});

export const setIsSearch = isSearch => ({
    type: consts.SET_IS_SEARCH,
    isSearch
});

export const saveKeyword = keyword => ({
    type: consts.SAVE_KEYWORD,
    keyword
});

export const setNextPageToken = nextPageToken => ({
    type: consts.SET_NEXT_PAGE_TOKEN,
    nextPageToken
});
