import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { NavLink, withRouter, Link } from 'react-router-dom';

import Logo from '../../assets/images/logo.svg';
import Search from '../../assets/images/search.svg';
import Routes from '../../App/routes';
import TopMenuRoutes from './routes';

import { setIsSearch, getHomePosts, saveKeyword } from '../Home/actions';

class Header extends Component {
    handleSearch = e => {
        const { getHomePosts, history, setIsSearch, saveKeyword } = this.props;
        const searchValue = e.target.value;
        if (e.key === 'Enter') {
            if (searchValue.length > 0) {
                setIsSearch(true);
                getHomePosts({ pageToken: null, keyword: searchValue });
                saveKeyword(searchValue);
                e.target.value = '';
                setTimeout(() => {
                    history.push('/');
                }, 400);
            }
        }
    };
    render() {
        return (
            <header>
                <div className="top-menu">
                    <div className="container">
                        <ul>
                            {TopMenuRoutes.map((route, key) => (
                                <li key={key}>
                                    <NavLink
                                        exact={route.exact}
                                        activeClassName="actived"
                                        to={route.path}
                                    >
                                        <img
                                            src={route.icon}
                                            alt={route.title}
                                        />
                                        {route.title}
                                    </NavLink>
                                </li>
                            ))}
                        </ul>
                        <div className="search">
                            <img src={Search} alt="" />
                            <input
                                placeholder="Tìm kiếm"
                                onKeyPress={e => this.handleSearch(e)}
                            />
                        </div>
                    </div>
                </div>
                <div className="header">
                    <div className="container">
                        <div className="logo">
                            <Link to="/">
                                <img src={Logo} alt="Xitashop" />
                            </Link>
                        </div>
                        <div className="main-menu">
                            <ul>
                                {Routes.map((route, key) => (
                                    <li key={key}>
                                        <NavLink
                                            exact={route.exact}
                                            activeClassName="actived"
                                            to={route.path}
                                        >
                                            {route.title}
                                        </NavLink>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

Header.propTypes = {
    getHomePosts: PropTypes.func.isRequired,
    history: PropTypes.object.isRequired,
    setIsSearch: PropTypes.func.isRequired,
    saveKeyword: PropTypes.func.isRequired
};

const mapStateToProps = () => {
    return {};
};

const mapDispatchToProps = {
    getHomePosts,
    setIsSearch,
    saveKeyword
};

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Header)
);
